@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Roboto:wght@300;400&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

nav, footer, header, aside {
  display: block; }

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit; }

/*--------------------*/
a {
  color: #000;
  text-decoration: none; }

a.text:active,
a.text:hover,
a.text {
  text-decoration: none;
  color: #666; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 1rem;
  color: #000; }

body {
  font-family: 'Roboto', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

.container {
  max-width: 100%;
  min-width: 320px; }

.wrap {
  min-width: 320px;
  max-width: 410px;
  padding: 0 1rem;
  margin: 0 auto;
  box-sizing: border-box;
  border: 1px solid black; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.header {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-content: center; }

.header__list {
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center; }

img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.form__wrap {
  box-shadow: white; }

.profile__head {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 2rem;
  line-height: 40px;
  text-align: left;
  margin-bottom: 21px; }

.registration__tabs {
  display: flex;
  justify-content: start;
  flex-direction: row;
  color: #6B6B6B;
  line-height: 24px; }
  .registration__tabs .tab {
    font-weight: 400; }
  .registration__tabs .tab:last-child {
    margin-left: 2rem; }

.tab-active {
  color: #000;
  padding-bottom: 15px;
  border-bottom: 2px solid #6E41E2; }

.social {
  background: #FAFAFA;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px; }
  .social p {
    font-family: 'Roboto';
    font-size: 0.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    text-align: left; }
  .social .social__icons {
    display: flex;
    flex-direction: row; }
    .social .social__icons .social__icons-img {
      max-width: 36px;
      max-height: 36px; }

.inputs {
  display: flex;
  flex-direction: column; }
  .inputs input {
    min-height: 56px;
    width: 100%;
    box-shadow: 0px 4px 16px 0px rgba(51, 51, 51, 0.08); }

.form__checkbox {
  font-size: 1rem;
  line-height: 24px;
  font-weight: 400px; }
  .form__checkbox a {
    color: blue; }

.btn__form {
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  background: #FFFFFF;
  color: #6E41E2;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 8px; }

.btn__form-active {
  color: #FFFFFF;
  background: #6E41E2; }

